import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileImportListResponse } from "../../Model/Response/FileImportListResponse";
import { FileImportResponse } from "../../Model/Response/FileImportResponse";
import { FileImportViewModel } from '../../Model/ViewModel/FileImportViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileImportServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileImportServicesProvider extends FileServicesProvider {
  private FileImports: FileImportListResponse;
  private FileImport: FileImportResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileImport";
    console.log("Hello FileImportServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileImport?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileImport/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileImports = new FileImportListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileImports;
    });
  }



  public PostPutAsync(FileImportViewModel:FileImportViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileImport";
    else
    sub_url = "FileImport/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileImportViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileImportViewModel,mode).map(
      item => {
        this.FileImport = new FileImportResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileImport;
      }
    );
  }
}



